import { Form, Input, Button } from 'antd';
import swal from 'sweetalert';
//REACT REDUX
import { useSelector, useDispatch } from "react-redux";
import React, { useEffect } from 'react'
import { checkAuthResetAction, removeResetUserAction, userAuthResetPwdAction } from '../../../redux/actions';
//HISTORY && ROUTER
import history from '../../../utils/history';
import { ROUTERS } from '../../../constants/router';

export default function ResetPassword(props) {
    
    const {match} = props;
    //REDUX 
    const dispatch = useDispatch();
    const user = useSelector(state => state.userReducer);
    const { resetUser } = user;
    useEffect(() => {
        dispatch(checkAuthResetAction(match.params.id));
        return () => {
            dispatch(removeResetUserAction())
        }
    }, []);
    if(resetUser === "token not found"){
        history.push('/')
    }
    if(resetUser === "token expired"){
        history.push(ROUTERS.FORGOT_PASSWORD)
        swal("ng dn  ht hn", "Vui lng gi li email khc", "error")
    }
    if(resetUser === "success"){
        history.push(ROUTERS.LOGIN)
        swal("Bn  i Mt Khu Thnh Cng","Vui Lng ng Nhp","success")
    }
    return (
        <Form
            style={{ width: "500px", margin: 'auto', marginTop: '50px' }}
            name="basic"
            layout="vertical"
            initialValues={{
                remember: true,
            }}
            onFinish={(value)=>{dispatch(userAuthResetPwdAction(value.password,resetUser.id_user))}}
        >
            <Form.Item
                name="password"
                className="form-in"
                rules={[
                    {
                        required: true,
                        message: 'Vui lng nhp mt khu!',
                    },
                    {
                        min: 8,
                        message: 'Mt khu ca bn phi di t nht 8 k t!'
                    },
                    {
                        whitespace: false,
                        message: 'Vui lng khng dng khong trng  t mt khu!'
                    },
                ]}
                hasFeedback
            >
                <Input.Password placeholder="Nhp mt khu mi..." />
            </Form.Item>

            <Form.Item
                name="confirm"
                className="form-in"
                dependencies={['password']}
                hasFeedback
                rules={[
                    {
                        required: true,
                        message: 'Vui lng nhp mt khu',
                    },
                    {
                        whitespace: false,
                        message: 'Vui lng khng dng khong trng  t mt khu!'
                    },
                    ({ getFieldValue }) => ({
                        validator(_, value) {
                            if (!value || getFieldValue('password') === value) {
                                return Promise.resolve();
                            }
                            return Promise.reject(new Error('Mt khu bn va nhp khng khp vi mt khu trc !'));
                        },
                    }),
                ]}
            >
                <Input.Password placeholder="Xc nhn mt khu..." />
            </Form.Item>
            <Form.Item>
                <Button className="btn-signin" type="primary" htmlType="submit">
                    RESET PASSWORD
                </Button>
            </Form.Item>
        </Form>
    )
}