import { useState } from "react"
import { EditTwoTone } from '@ant-design/icons';
import { Select, Form, Button, Tag } from "antd";
export default function UpdateStatus(props) {
    const { trangthai, id_donhang, UPDATEORDERSTATUS } = props
    const [isEdit, setIsEdit] = useState(false);
    function renderItemView() {
        return (
            <>
                {trangthai === 0 && <Tag color="geekblue">Ch Xc Nhn</Tag>}
                {trangthai === 1 && <Tag color="lime"> Xc Nhn</Tag>}
                {trangthai === 2 && <Tag color="blue">ang Giao Hng</Tag>}
                {trangthai === 3 && <Tag color="green">Giao Hng Thnh Cng</Tag>}
                {trangthai === 4 && <Tag color="red">Hy n</Tag>}
                <EditTwoTone onClick={() => setIsEdit(true)} twoToneColor="#eb2f96" style={{ fontSize: '25px', marginLeft: '20px' }} />
            </>
        )
    }

    function renderItemEdit() {
        return (
            <>
                <Form
                    layout="vertical"
                    onFinish={(values) => {
                        UPDATEORDERSTATUS(values, id_donhang);
                        setIsEdit(false);
                    }}
                >
                    <Form.Item
                        name="trangthai"
                        rules={[{ required: true, message: 'Please input your trng thi' }]}
                    >
                        <Select placeholder="Chn tnh trng...">
                            <Select.Option value="1"> Xc Nhn</Select.Option>
                            <Select.Option value="2">ang Giao Hng</Select.Option>
                            <Select.Option value="3">Giao Hng Thnh Cng</Select.Option>
                            <Select.Option value="4">Hy n</Select.Option>
                        </Select>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit">
                            LU
                        </Button>
                        <Button type="danger" onClick={()=>{setIsEdit(false)}}>
                            HY
                        </Button>
                    </Form.Item>
                </Form>

            </>
        )
    }
    return (
        isEdit ? renderItemEdit() : renderItemView()
    )
}