import React from 'react'
//Components
import { ROUTERS } from '../../constants/router'
import history from '../../utils/history'
//CSS
import { Form, Input } from 'antd';
import { CheckOutlined, ArrowLeftOutlined } from '@ant-design/icons';
//REACT REDUX
import { useSelector, useDispatch } from "react-redux";
//Action
import { updateUserInfoAction } from '../../redux/actions';
function EditProfile() {
    const dispatch = useDispatch();
    const getUserInfo = useSelector((state) => state.userReducer);
    const { userInfo } = getUserInfo;
    const [editForm] = Form.useForm();
    if(userInfo.length === 0){  
        history.push(ROUTERS.LOGIN)
    }
    return (
        <div className="content">
            <div className="profile-holder">
                <div className="user-profile-details">
                    <h4>Thay i Thng Tin C Nhn</h4>

                    <Form
                        // {...formItemLayout}
                        form={editForm}
                        layout='vertical'
                        initialValues={
                            {
                                hoten: userInfo[0]?.hoten,
                                diachi: userInfo[0]?.diachi,
                                email: userInfo[0]?.email,
                                sodienthoai: userInfo[0]?.sodienthoai,
                            }
                        }
                        onFinish={(values)=>{
                            const newVal = {
                                ...values,
                                id_user: userInfo[0]?.id_user,
                            }
                            dispatch(updateUserInfoAction(newVal));
                        }}
                    >
                        <Form.Item
                            label="H Tn"
                            name="hoten"
                            className="full-name"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp H Tn!',
                                },
                            ]}
                        >
                            <Input placeholder="H Tn" />
                        </Form.Item>
                        <Form.Item
                            label="Email"
                            className="form-in"
                            name="email"
                            rules={[
                                {
                                    type: 'email',
                                    message: 'Email bn va nhp khng ng!',
                                },
                                {
                                    required: true,
                                    message: 'Vui lng nhp Email!',
                                },
                            ]}
                        >
                            <Input placeholder="Nhp email ca bn..." />
                        </Form.Item>
                        <Form.Item
                            name="diachi"
                            label="a Ch"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp a ch!',
                                },
                            ]}
                        >
                            <Input placeholder="Nhp a ch..." />
                        </Form.Item>
                        <Form.Item
                            name="sodienthoai"
                            label="S in thoi"
                            rules={[
                                { required: true, 
                                    message: 'Vui lng nhp s in thoi!' 
                                },
                                {
                                    min: 9,
                                    message: 'S in thoi bn va nhp khng ng nh dng'

                                },
                                {
                                    required: true, 
                                    pattern: new RegExp("^[0-9]*$"),
                                    message: "S in thoi ch bao gm s, khng bao gm k t khc!"
                                  }
                            ]}
                        >
                            <Input placeholder="Nhp s in thoi..." addonBefore="+84" style={{ width: '100%' }} minLength="9" maxlength="10"/>
                        </Form.Item>

                    </Form>
                </div>
                <div className="checkout-shipping-action">

                <button onClick={() => history.push(ROUTERS.PROFILE)} className="button-muted back-button"
                    type="button"
                >
                    <ArrowLeftOutlined /> &nbsp;
                    Quay Li
                </button>

                <button onClick={() => editForm.submit()} className="next-step-button complete-button"
                    type="button"
                >
                    <CheckOutlined />
                    &nbsp;
                    Xc nhn
                </button>
            </div>
            </div>
        </div>
    )
}

export default EditProfile
