//CSS
import { Button, Form, Row, Input, Spin } from "antd";
import { Editor } from '@tinymce/tinymce-react';
import { ChromePicker } from 'react-color';
//REDUX && REACT
import { useSelector, useDispatch } from "react-redux";
import { useState } from 'react';
import swal from "sweetalert";
import { uploadAnhBia } from "../../../redux/actions/admin.action";
//
export default function AdminAddNews() {
    const { TextArea } = Input;
    const dispatch = useDispatch();
    const getUser = useSelector(state => state.userReducer);
    const { userInfo } = getUser;
    const getNews = useSelector(state => state.adminReducer);
    const { loading } = getNews;
    const [selectedFile, setSelectedFile] = useState(null);
    const [content, setContent] = useState("");
    //COLOR
    const [colorValue, setColorValue] = useState('#ddd');
    function setFile(e) {
        setSelectedFile(e.target.files)
    }
    function handleEditorChange(content, editor) {
        setContent(content)
    }
    function onFinish(values) {
        let newValues = {
            ...values,
            content,
            maubia: colorValue,
        }
        if (content === '' || selectedFile === null) {
            swal("Vui lng chn nh hoc in ni dung", "", "error")
        } else {
            const formData = new FormData();
            formData.append("file", selectedFile[0]);
            formData.append("upload_preset", "tccvzctk");
            dispatch(uploadAnhBia(formData, newValues))
        }
    }
    return (
        <Row>
            <div style={{ textAlign: 'center', width: '100%', backgroundColor: '#f9f9f9' }}>
                <h4 style={{ marginTop: '30px' }}>Thm Tin Tc</h4>
            </div>
            <div className="content3">
                <div className="profile-holder">
                    <div class="form-group files">
                        <label>nh Ba</label>
                        <input type="file" className="form-control" onChange={setFile} accept="image/*" />
                    </div>
                    <div style={{ marginBottom: '20px' }}>
                        <label>Chn mu ba</label>
                        <ChromePicker
                            color={colorValue}
                            onChange={(color)=>setColorValue(color.hex)}
                        />
                    </div>
                    <Form
                        onFinish={onFinish}
                        initialValues={
                            {
                                nguoiviet: userInfo[0]?.hoten,
                            }
                        }
                    >
                        <Form.Item
                            name="tieude"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp tiu !',
                                },
                            ]}
                        >
                            <TextArea showCount maxLength={100} placeholder="Nhp tiu ..." />
                        </Form.Item>
                        <Form.Item
                            name="nguoiviet"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp tn!',
                                },
                            ]}
                        >
                            <Input style={{ width: '100%' }} />
                        </Form.Item>
                        <Form.Item
                            name="loaitintuc"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp loi tin!',
                                },
                            ]}
                        >
                            <Input style={{ width: '100%' }} placeholder="Loi Tin Tc..." />
                        </Form.Item>
                        <Form.Item
                            name="tomtat"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp tm tt!',
                                },
                            ]}
                        >
                            <TextArea showCount maxLength={150} placeholder="Nhp tm tt..." />
                        </Form.Item>
                        <Editor
                            apiKey='vay56zrvh9veuhcbway7pupzaoycpwvx06k3t4xzzsv3lfgv'
                            initialValue="<p>y l ni dung mc nh</p>"
                            init={{
                                height: 500,
                                menubar: true,
                                plugins: [
                                    'advlist autolink lists link image charmap print preview anchor',
                                    'searchreplace visualblocks code fullscreen',
                                    'insertdatetime media table paste code help wordcount'
                                ],
                                toolbar:
                                    'undo redo | formatselect | bold italic backcolor | \
                                                alignleft aligncenter alignright alignjustify | \
                                                bullist numlist outdent indent | removeformat | help'
                            }}
                            onEditorChange={handleEditorChange}
                        />
                        <Form.Item style={{ marginTop: '20px' }}>
                            <Button type="primary" htmlType="submit">
                                Thm Bi Vit
                            </Button>
                        </Form.Item>
                    </Form>
                </div>
                {loading && <Spin size="large" />}
            </div>
        </Row>
    )
}