import { useState } from "react"
import { EditTwoTone } from '@ant-design/icons';
import { Select, Form, Button } from "antd";
export default function UpdateStatus(props) {
    const {trangthai,id_size, UPDATESIZESTATUS} = props
    const [isEdit, setIsEdit] = useState(false);
    function renderItemView() {
        return (
            <>
                <span>{trangthai === 1 ? "Kch Hot" : "Khng Kch Hot"}</span>
                <EditTwoTone onClick={() => setIsEdit(true)} twoToneColor="#eb2f96" style={{ fontSize: '25px', marginLeft: '20px' }} />
            </>
        )
    }

    function renderItemEdit() {
        return (
            <>
                <Form
                    layout="vertical"
                    onFinish={(values) => {
                        UPDATESIZESTATUS(values, id_size);
                        setIsEdit(false);
                    }}
                >
                    <Form.Item
                        name="trangthai"
                        rules={[{ required: true, message: 'Please input your trng thi' }]}
                    >
                        <Select placeholder="Chn tnh trng...">
                            <Select.Option value="1">Kch Hot</Select.Option>
                            <Select.Option value="0">Khng Kch Hot</Select.Option>
                        </Select>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit">
                            SAVE
                        </Button>
                    </Form.Item>
                </Form>

            </>
        )
    }
    return (
        isEdit ? renderItemEdit() : renderItemView()
    )
}