//CSS
import { Form, Input, Button, InputNumber, Select, Row, Col, Breadcrumb, Image } from 'antd';
import { Link } from 'react-router-dom';
import { ROUTERS } from '../../../../constants/router';
//REDUX && REACT
import { useSelector, useDispatch } from "react-redux";
import { getShoeAction, getShoeTypeAction, removeShoeAction, removeShoeTypeAction, upLoadFile, updateShoeAction } from "../../../../redux/actions";
import { useEffect, useState } from 'react';
//COMPONENTS

export default function AdminUpdateProduct(props) {
    const { match } = props
    const dispatch = useDispatch();
    const getShoe = useSelector(state => state.productReducer);
    const getShoeType = useSelector(state => state.adminReducer);
    const { shoeList } = getShoe;
    const { shoeTypeList, loading, error } = getShoeType
    //lc giy theo id , lc kiu giy theo id 
    const shoeOnUpdate = shoeList.filter(item => item.id_giay === parseInt(match.params.id))
    const shoeTypeOnUpdate = shoeTypeList.filter(item => item.id_kieugiay === shoeOnUpdate[0].id_kieugiay)
    useEffect(() => {
        dispatch(getShoeAction())
        dispatch(getShoeTypeAction());
        return () => {
            dispatch(removeShoeAction());
            dispatch(removeShoeTypeAction());
        }
    }, []);
    const [selectedFile, setSelectedFile] = useState(null);
    function onFinish(values) {
        let hinhanh = '';
        selectedFile ? hinhanh = selectedFile[0].name : hinhanh = shoeOnUpdate[0]?.hinhanh
        let id_giay = match.params.id;
        const newUpdate = {
            ...values,
            id_giay,
            hinhanh,
        }
        if (selectedFile) {
            dispatch(updateShoeAction(newUpdate));
            dispatch(upLoadFile(selectedFile));
        } else {
            dispatch(updateShoeAction(newUpdate));
        }
    }
    function updateImage(e) {
        setSelectedFile(e.target.files)
    }
    const [updateForm] = Form.useForm();
    return (
        <>
            <div style={{ textAlign: 'center', width: '100%', backgroundColor: '#f9f9f9' }}>
                <h4>Cp Nht Giy</h4>
            </div>
            <Breadcrumb style={{ marginTop: '20px', marginLeft: '20px' }}>
                <Breadcrumb.Item><Link to={ROUTERS.READ_PRODUCT}>Xem Giy</Link></Breadcrumb.Item>
                <Breadcrumb.Item>Cp Nht Giy</Breadcrumb.Item>
            </Breadcrumb>
            <Row style={{ marginTop: '30px' }}>
                <Col span={12} offset={6}>
                    <Form
                        form={updateForm}
                        initialValues={{
                            tengiay: shoeOnUpdate[0]?.tengiay,
                            tenmagiay: shoeOnUpdate[0]?.tenmagiay,
                            giaban: shoeOnUpdate[0]?.giaban,
                            giakm: shoeOnUpdate[0]?.giakm,
                            noidung: shoeOnUpdate[0]?.noidung,
                        }}
                        onFinish={onFinish}
                    >
                        <Form.Item
                            name="tengiay"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp tn giy...',
                                },
                            ]}
                        >
                            <Input placeholder="Nhp tn giy..." />
                        </Form.Item>

                        <Form.Item

                            name="tenmagiay"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp tn m giy...',
                                },
                            ]}
                        >
                            <Input placeholder="Nhp tn m giy..." />
                        </Form.Item>

                        <Form.Item
                            label="Gi Bn: "
                            name="giaban"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp gi bn...',
                                },
                            ]}
                        >
                            <InputNumber
                                style={{ width: 500 }}
                                min={1}
                                max={10000000}
                                formatter={value => `VN ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                parser={value => value.replace(/\VN\s?|(,*)/g, '')}
                            // onChange={onChange}
                            />
                        </Form.Item>

                        <Form.Item
                            name="giakm"
                            label="Gi KM: "
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp gi khuyn mi...',
                                },
                            ]}
                        >
                            <InputNumber
                                style={{ width: 500 }}
                                min={1}
                                max={10000000}
                                formatter={value => `VN ${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ',')}
                                parser={value => value.replace(/\VN\s?|(,*)/g, '')}
                            // onChange={onChange}
                            />
                        </Form.Item>

                        <Form.Item
                            name="noidung"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp ni dung...',
                                },
                            ]}
                        >
                            <Input placeholder="Nhp ni dung..." />
                        </Form.Item>

                        <Form.Item
                            name="id_kieugiay"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng chn kiu giy...',
                                },
                            ]}
                        >
                            <Select placeholder={shoeTypeOnUpdate[0]?.tenkieugiay}>
                                {shoeTypeList.map((item, index) => {
                                    return <Select.Option key={index} value={item.id_kieugiay}>{item.tenkieugiay}</Select.Option>
                                })}
                            </Select>
                        </Form.Item>

                        <Form.Item
                            name="tinhtrang"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng chn tnh trng...',
                                },
                            ]}
                        >
                            <Select placeholder={shoeTypeOnUpdate[0]?.tinhtrang === 0 ? 'Khng Kch Hot' : 'Kch Hot'}>
                                <Select.Option value="1">Kch Hot</Select.Option>
                                <Select.Option value="0">Khng Kch Hot</Select.Option>
                            </Select>
                        </Form.Item>

                        <div class="form-group files">
                            <label>Hnh nh i Din</label>
                            <input type="file" class="form-control" onChange={updateImage} />
                        </div>
                        {loading
                            ? <div>Uploading...</div>
                            : error
                                ? <div>{error}</div>
                                : <div>Please Choose Image</div>}
                        <Image
                            height={300}
                            width={450}
                            src={`/gallery/${shoeOnUpdate[0]?.hinhanh}`}
                        />
                        <Form.Item >
                            <Button type="primary" htmlType="submit">
                                LU
                            </Button>
                        </Form.Item>
                    </Form>
                </Col>
            </Row>
        </>
    )
}