import { Menu } from 'antd';
import { Link } from 'react-router-dom';
import { useState } from 'react';
import { ROUTERS } from '../../constants/router';
import logo from '../../img/hgny.png'

const { SubMenu } = Menu;
export default function MenuAdmin() {
    const [current, setCurrent] = useState();
    const handleClick = e => {
        setCurrent(e.key)
    };
    return (
        <Menu onClick={handleClick} selectedKeys={[current]} mode="horizontal">
            <Menu.Item key="setting:8"><Link to={ROUTERS.HOME}><img width='140px' height='30px' src={logo} /></Link></Menu.Item>
            <SubMenu key="shoe" title="Qun L Giy">
                <Menu.Item key="setting:1"><Link to={ROUTERS.READ_PRODUCT}>Xem/Sa Giy</Link></Menu.Item>
                <Menu.Item key="setting:2"><Link to={ROUTERS.ADD_PRODUCT}>Thm Giy</Link></Menu.Item>
                <SubMenu key="color" title="Qun L Mu">
                    <Menu.Item key="setting:3"><Link to={ROUTERS.READ_COLOR}>Xem/Sa Mu</Link></Menu.Item>
                    <Menu.Item key="setting:4"><Link to={ROUTERS.ADD_COLOR}>Thm Mu</Link></Menu.Item>
                </SubMenu>
                <SubMenu key="size" title="Qun L Size">
                    <Menu.Item key="setting:5"><Link to={ROUTERS.READ_SIZE}>Xem/Sa Size</Link></Menu.Item>
                    <Menu.Item key="setting:6"><Link to={ROUTERS.ADD_SIZE}>Thm Size</Link></Menu.Item>
                </SubMenu>
                <SubMenu key="type" title="Qun L Kiu Giy">
                    <Menu.Item key="setting:11"><Link to={ROUTERS.READ_TYPE}>Xem/Sa Kiu Giy</Link></Menu.Item>
                    <Menu.Item key="setting:12"><Link to={ROUTERS.ADD_TYPE}>Thm Kiu Giy</Link></Menu.Item>
                </SubMenu>
            </SubMenu>
            <Menu.Item key="setting:7"><Link to={ROUTERS.READ_ORDER}>Qun L Ha n</Link></Menu.Item>
            <Menu.Item key="setting:9"><Link to={ROUTERS.READ_USER}>Qun L Ngi Dng</Link></Menu.Item>
            <Menu.Item key="setting:10"><Link to={ROUTERS.STATISTIC}>Qun L Thng K</Link></Menu.Item>
            <SubMenu key="sales" title="Qun L Khuyn Mi">
                <Menu.Item key="addsale"><Link to={ROUTERS.ADD_SALE}>Thm Khuyn Mi</Link></Menu.Item>
                <Menu.Item key="editsale"><Link to={ROUTERS.READ_SALE}>Xem/Sa Khuyn Mi</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="banner" title="Qun L Banner">
                <Menu.Item key="addbanner"><Link to={ROUTERS.ADD_BANNER}>Thm Banner</Link></Menu.Item>
                <Menu.Item key="editbanner"><Link to={ROUTERS.READ_BANNER}>Xem/Sa Banner</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="news" title="Qun L Tin Tc">
                <Menu.Item key="addnews"><Link to={ROUTERS.ADD_NEWS}>Thm Tin Tc</Link></Menu.Item>
                <Menu.Item key="editnews"><Link to={ROUTERS.READ_NEWS}>Xem/Sa Tin Tc</Link></Menu.Item>
            </SubMenu>
        </Menu>
    )
}