import { Form, Row, Select, Button, Input } from "antd";
//REDUX && REACT
import { useDispatch } from "react-redux";
import { createTypeAction } from "../../../redux/actions";
export default function AdminAddType() {
    const dispatch = useDispatch();
    function onFinish(values) {
        dispatch(createTypeAction(values))
    }
    return (
        <Row>
            <div style={{ textAlign: 'center', width: '100%', backgroundColor: '#f9f9f9' }}>
                <h4 style={{ marginTop: '30px' }}>Thm Kiu Giy</h4>
            </div>
            <div className="content3">
                <div className="profile-holder">
                    <Form onFinish={onFinish}>
                        <Form.Item
                            name="tenkieugiay"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng nhp tn kiu giy!',
                                },
                            ]}
                        >
                            <Input placeholder="Nhp tn kiu giy..." style={{ width: '100%' }} />
                        </Form.Item>
                        <Form.Item
                            name="tinhtrang"
                            rules={[
                                {
                                    required: true,
                                    message: 'Vui lng chn tnh trng...',
                                },
                            ]}
                        >
                            <Select placeholder="Chn tnh trng..." >
                                <Select.Option value="1">Kch Hot</Select.Option>
                                <Select.Option value="0">Khng Kch Hot</Select.Option>
                            </Select>
                        </Form.Item>
                        <Form.Item>
                            <Button type="primary" htmlType="submit">
                                Thm Kiu Giy
                        </Button>
                        </Form.Item>
                    </Form>
           </div>
           </div>
        </Row>
    )
}