import { Form, Row, Select, Button, Input } from "antd";
//REDUX && REACT
import { useDispatch } from "react-redux";
import { createSizeAction } from "../../../redux/actions";

export default function AdminAddSize() {
    const dispatch = useDispatch();
    function onFinish(values) {
        dispatch(createSizeAction(values))
    }
    return (
        <Row >
            <div style={{ textAlign: 'center', width: '100%', backgroundColor: '#f9f9f9' }}>
                <h4>Thm Kch Thc</h4>
            </div>
            <div className="content3">
                <div className="profile-holder">
                <Form onFinish={onFinish}>
                    <Form.Item
                        name="sosize"
                        rules={[
                            {
                                required: true,
                                message: 'Vui lng nhp size giy!',
                            },
                            {
                                required: true,
                                pattern: /^(?:\d*)$/,
                                message: "Size giy ch bao gm k t s",

                            },
                            {
                                max: 2,
                                message: 'S size va nhp khng ng'

                            },
                        ]}
                    >
                        <Input placeholder="Nhp S Size" style={{ width: '100%' }}  />
                    </Form.Item>
                    <Form.Item
                        name="trangthai"
                        rules={[
                            {
                                required: true,
                                message: 'Vui lng chn trng thi...',
                            },
                        ]}
                    >
                        <Select placeholder="Chn tnh trng..." style={{width: '100%'}}>
                            <Select.Option value="1">Kch Hot</Select.Option>
                            <Select.Option value="0">Khng Kch Hot</Select.Option>
                        </Select>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit">
                            To Size
                        </Button>
                    </Form.Item>
                </Form>
            </div>
            </div>
        </Row>
    )
}