import React, { useEffect } from 'react'
//REDUX
import { useDispatch, useSelector } from "react-redux";
import { clearCart , changeCheckoutStatus } from '../../../redux/actions'
//Components
import history from '../../../utils/history';
import { ROUTERS } from '../../../constants/router';
import { StepTracker } from '../components'

function Payment() {
    const getCheckout = useSelector(state => state.checkoutReducer);
    const { checkout } = getCheckout;
    if (checkout === false) {
        history.push(ROUTERS.HOME);
    }
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(clearCart());
        return () => {
            dispatch(changeCheckoutStatus());
        }
    }, []);
    
    
    return (
        <div className="checkout-step3">
            <StepTracker current={3} />
            <div className="checkout-step3">
                <h4>n hng ca bn  c ghi nhn!</h4>
                <p style={{ textAlign: 'center' }}>Thi gian giao hng khong 4 - 5 ngy.</p>
                <h4>Cm n qu khch  tin tng mua hng ca chng ti!</h4>
            </div>
        </div>
    )
}

export default Payment
