import React, { useState } from 'react'
import "antd/dist/antd.css";
import { Form, Input, Select } from 'antd';
import { ArrowRightOutlined, ArrowLeftOutlined } from '@ant-design/icons';
//Components
import { ROUTERS } from '../../../constants/router'
import history from '../../../utils/history'
//Redux
import { useSelector, useDispatch } from "react-redux";
import { createInvoice } from '../../../redux/actions'

function ShippingForm() {
    const getUser = useSelector(state => state.userReducer);
    const getCart = useSelector(state => state.cartReducer);
    const getLoading = useSelector(state => state.checkoutReducer);
    const { userInfo } = getUser;
    const { cartItems } = getCart;
    const { loading } = getLoading;
    if (cartItems.length === 0) {
        history.push(ROUTERS.CHECKOUT_STEP_1)
    }
    let totalPrice = 0;
    function calculateTotal(arr) {
        totalPrice = arr.reduce((acc, val) => acc + val, 0);
    };
    calculateTotal(cartItems.map((item) => item.giakm * item.soluong))
    const dispatch = useDispatch();
    const [shippingForm] = Form.useForm();
    const [payment, setPayment] = useState('');
    const formLayout = 'vertical';
    const onFinish = (values) => {
        const invoice = {
            ...values,
            id_user: userInfo[0]?.id_user,
            cartItems,
            totalPrice,
        }
        dispatch(createInvoice(invoice));
    };
    function handleChange(value) {
        setPayment(value)
    }
    return (
        <>
            <div className="checkout-shipping-wrapper">
                <div className="checkout-shipping-form">
                    <div className="checkout-fieldset">
                        <Form
                            // {...formItemLayout}
                            form={shippingForm}
                            layout={formLayout}
                            initialValues={
                                userInfo.length > 0
                                    ? {
                                        hoten: userInfo[0]?.hoten,
                                        email: userInfo[0]?.email,
                                        diachi: userInfo[0]?.diachi,
                                        sodienthoai: userInfo[0]?.sodienthoai,
                                    }
                                    : {
                                        remember: true,
                                    }
                            }
                            onFinish={onFinish}
                        >
                            <Form.Item
                                label="H Tn"
                                name="hoten"
                                className="full-name"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp H Tn!',
                                    },
                                ]}
                            >
                                <Input placeholder="H Tn" />
                            </Form.Item>
                            <Form.Item
                                label="Email"
                                className="form-in"
                                name="email"
                                rules={[
                                    {
                                        type: 'email',
                                        message: 'Email bn va nhp khng ng!',
                                    },
                                    {
                                        required: true,
                                        message: 'Vui lng nhp Email!',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhp email ca bn..." />
                            </Form.Item>
                            <Form.Item
                                name="diachi"
                                label="a Ch"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp a ch!',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhp a ch..." />
                            </Form.Item>
                            <Form.Item
                                name="sodienthoai"
                                label="S in thoi"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng nhp s in thoi!',
                                    },
                                ]}
                            >
                                <Input placeholder="Nhp s in thoi..." addonBefore="+84" style={{ width: '100%' }} />
                            </Form.Item>

                            <Form.Item
                                label="Phng Thc Thanh Ton"
                                name="loaithanhtoan"
                                rules={[
                                    {
                                        required: true,
                                        message: 'Vui lng chn loi thanh ton...!',
                                    },
                                ]}
                            >
                                <Select placeholder="Chn phng thc thanh ton..." onChange={handleChange}>
                                    <Select.Option value="cod">COD - Thanh ton khi nhn hng</Select.Option>
                                    <Select.Option value="online">Thanh ton online - Chuyn khon</Select.Option>
                                </Select>
                            </Form.Item>
                            {payment === "online"
                                ? <div className="checkout-collapse-sub checkout-fieldset-collapse">
                                    <p>Thng Tin Chuyn Khon:</p>
                                    <p>VIETCOMBANK - 0041000311172</p>
                                    <p>Tran Ha Nam</p>
                                </div>
                                : null
                            }

                        </Form>
                    </div>
                </div>
            </div>
            {loading && <div style={{textAlign:'center'}}>ang Thanh Ton...</div>}
            <div className="checkout-shipping-action">

                <button onClick={() => history.push(ROUTERS.CHECKOUT_STEP_1)} className="button-muted"
                    type="button"
                >
                    <ArrowLeftOutlined /> &nbsp;
                    Quay Li
                </button>

                <button onClick={() => shippingForm.submit()} className="next-step-button"
                    type="button"
                >
                    Xc nhn
                    &nbsp;
                    <ArrowRightOutlined />
                </button>
            </div>
        </>
    )
}

export default ShippingForm;
