import { Form, Input, Button } from 'antd';
import { LoadingOutlined } from '@ant-design/icons';
import Countdown from "react-countdown";
//REACT && REDUX
import { useState } from 'react'
import { useDispatch, useSelector } from "react-redux";
import { userResetPasswordAction } from '../../../redux/actions';
export default function ForgotPassword() {
    const dispatch = useDispatch();
    const user = useSelector(state => state.userReducer);
    const { loadingReset, checkTime } = user;
    const [disabled, setDisabled] = useState(false)
    //COUNT DOWN

    // Renderer callback with condition
    const renderer = ({ minutes, seconds, completed }) => {
        if (completed) {
            setDisabled(false);
            return null;
        } else {
            // Render a countdown
            setDisabled(true);
            return (
                <>
                    <span>
                        Vui lng i <span style={{ color: 'red' }}> {minutes}:{seconds} </span> trc khi gi mail khc!
                </span>
                    <h4>Vui lng kim tra c mc Th Rc ca bn!</h4>
                </>
            );
        }
    };
    console.log(checkTime)
    return (
        <Form
            style={{ width: "500px", margin: 'auto', marginTop: '50px' }}
            name="basic"
            layout="vertical"
            initialValues={{
                remember: true,
            }}
            onFinish={(values) => {
                dispatch(userResetPasswordAction(values.email));
                setDisabled(true);
            }}
        >
            <Form.Item
                label="Email:"
                className="form-in"
                name="email"
                rules={[
                    {
                        type: 'email',
                        message: 'Email bn nhp khng hp l!',
                    },
                    {
                        required: true,
                        message: 'Vui lng nhp Email!',
                    },
                ]}
            >
                <Input placeholder="Nhp email ca bn..." />
            </Form.Item>
            {checkTime.tokenTime && <Countdown date={checkTime && checkTime.tokenTime} renderer={renderer} />}
            <Form.Item>
                {!disabled && (
                    <Button className="btn-signin" type="primary" htmlType="submit">
                        Gi Mail
                    </Button>
                )}

            </Form.Item>
            {loadingReset && (
                <LoadingOutlined style={{ fontSize: 40 }} spin />
            )}
        </Form>
    )
}