import { useState } from "react"
import { EditTwoTone } from '@ant-design/icons';
import { Select, Form, Button } from "antd";
export default function UpdateRole(props) {
    const {role,id_user, UPDATEROLE} = props
    const [isEdit, setIsEdit] = useState(false);
    function renderItemView() {
        return (
            <>
                <span>{role}</span>
                <EditTwoTone onClick={() => setIsEdit(true)} twoToneColor="#eb2f96" style={{ fontSize: '25px', marginLeft: '20px' }} />
            </>
        )
    }

    function renderItemEdit() {
        return (
            <>
                <Form
                    layout="vertical"
                    onFinish={(values) => {
                        UPDATEROLE(values, id_user);
                        setIsEdit(false);
                    }}
                >
                    <Form.Item
                        name="role"
                        rules={[{ required: true, message: 'Please input your role' }]}
                    >
                        <Select placeholder="Chn tnh trng...">
                            <Select.Option value="admin">Qun L</Select.Option>
                            <Select.Option value="khachhang">Khch Hng</Select.Option>
                        </Select>
                    </Form.Item>
                    <Form.Item>
                        <Button type="primary" htmlType="submit">
                            SAVE
                        </Button>
                    </Form.Item>
                </Form>

            </>
        )
    }
    return (
        isEdit ? renderItemEdit() : renderItemView()
    )
}